import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-section-image-only',
  templateUrl: './section-image-only.component.html',
  styleUrls: ['./section-image-only.component.css']
})
export class SectionImageOnlyComponent implements OnInit {

  @Input() imageSrc : string;
  @Input() textShow : string;

  @Output() next: EventEmitter<any> = new EventEmitter();

  rotate = false;

  constructor() {
    
  }

  ngOnInit() {
  }

  clicked() {
    this.rotate = true;
    this.next.emit("ToDo: send noods");
  }
}
