import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { StoryBit } from '../_model/storybit';
import { log } from 'util';

declare var $;

@Component({
  selector: 'app-section-image-left-text-right',
  templateUrl: './section-image-left-text-right.component.html',
  styleUrls: ['./section-image-left-text-right.component.css']
})
export class SectionImageLeftTextRightComponent implements OnInit {

  @Input() imageSrc: string;
  @Input() imageSelectSrc: string;
  @Input() textRight: string;
  @Input() textChoiceResult: string[];

  @Input() storyBit: StoryBit;

  @Output() choice: EventEmitter<StoryBit> = new EventEmitter();

  slide: boolean = false;
  hasChosen: number = -1;

  map : any;

  constructor() { }

  ngOnInit() {

  }

  ngAfterViewInit() {
    console.log("here we go");
    let areas = this.storyBit.nextBits.map((nextBit) => {
      return {
        key: `${nextBit.id}`,
        fillColor: `ffffff`,
        toolTip: nextBit.choiceText,
      }
    })
    console.log(areas);

    this.map = $(`#img_${this.storyBit.id}`).mapster({
      // fillOpacity: 1.0,
      
      fillOpacity: 0.1,
      // fillColor: "d42e16",
      // stroke: false,
      // strokeColor: "ffffff",
      // strokeOpacity: 0.4,
      // strokeWidth: 4,

      fadeDuration: 80,
      singleSelect: true,
      mapKey: 'name',
      // render_highlight: {
      //   fillColor: '2aff00',
      //   stroke: false,
      //   altImage: this.imageSelectSrc
      // },
      // render_select: {
      //     fillColor: 'ff000c',
      //     stroke: false,
      //     altImage: this.imageSelectSrc
      // },
      showToolTip: true,
      toolTipClose: ["area-mouseout"],
      areas: areas,
    })

    console.log(this.map);
  }

  mouseEnter(s) {
    console.log(s);
  }

  chosen(choice) {
    this.slide = true;
    this.choice.emit(this.storyBit.nextBits[choice]);
    this.hasChosen = choice;
    $(`#img_${this.storyBit.id}`).mapster('tooltip');
  }

}
