import { StoryBit } from 'src/app/_model/storybit';


export function findStoryBit(storyId: string, storyList : StoryBit[]) {
    return storyList.find((story) => story.id == storyId);
}

export function node(storyBits: StoryBit[], id: string, nextNodes : StoryBit[] = []) {
    let story = findStoryBit(id, storyBits);
    if (story != null) {
        if (nextNodes.length != 0) {
            story.nextBits = nextNodes;
        }
        return story;
    }
    else {
        console.error("story id not found: ", id);
    }
}