import { Component } from '@angular/core';
import { StoryBit } from './_model/storybit';
import { buildStory } from 'src/stories/small_story';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ggj2019';

  story : StoryBit[];
  flash : boolean;
  playerName : string;

  ngOnInit() {
  }

  setPlayerName(name) {
    this.story = [buildStory()];
    this.playerName = name;
  }

  onNext(nextStory: StoryBit) {
    this.story.unshift( nextStory );
    this.flash = true;

    setTimeout(() => {
      this.flash = false;
    }, 500)
  }
}
